.menu {
  position: fixed;
  z-index: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
  max-width: var(--mobileMenuWidth);
  display: none;
  overflow-y: auto;
  transition: all 0s linear !important;
  /*flex-wrap: wrap;*/
  overflow: auto;
  overflow-x: hidden;
  align-items: flex-start !important; /* TODO remove important. change flex-center-all or add custom flex styles */
  height: 100%;
  min-height: 100%;
  max-height: 100vh;
  background-color: #24292e;
  display: flex;

  // .menu-overlay {
  //   position: relative;
  //   top: 0;
  //   right: 0;
  //   bottom: 0;
  //   left: 0;
  //   padding-top: 6rem;
  //   padding-bottom: 4rem;
  //   /*flex-wrap: wrap;*/
  //   overflow: auto;
  //   overflow-x: hidden;
  //   align-items: flex-start !important; /* TODO remove important. change flex-center-all or add custom flex styles */
  //   height: 100%;
  //   min-height: 100%;
  //   max-height: 100vh;
  //   background-color: rgba(40, 52, 60, 0.73);
  //   display: flex;

  // }

  .menu-header {
    height: 4.5rem;
    min-height: 4.5rem;
    width: 100%;
    padding: 0 2rem;
    background-color: #24292e;
    border-bottom: 1px solid #414141;
    z-index: 0;
    transition: all 0s linear !important;

    h1 {
      margin: 0;
      color: #fff;
      border: none;
      font-size: 1.2rem;
    }
  }

  nav {
    text-align: left;
    height: 100%;
    max-height: 30rem;
    min-height: 20rem;
    padding-left: 5%;
    padding: 1.25rem 5%;
    overflow-y: auto;

    a {
      font-size: 1rem;

      .icon-column {
        width: 3rem;
      }
    }
  }

  // 'a' is outside nav to also style the illLive link
  a {
    display: flex;
    align-items: center;
    color: #fff;
    padding: 0;
    margin: 0;
    border: none;
    border-bottom: none;
    text-shadow: none;
    font-weight: 100;
    text-align: left;
    width: 100%;

    &:nth-child(0) {
      margin-top: 4rem !important;
      border-bottom: none;
    }
  }

  .close-menu {
    position: relative;
    width: 1.438rem;
    height: 3rem;
    margin-right: 0.75rem;
    cursor: pointer;

    span {
      display: block;
      position: absolute;
      top: 1.5rem;
      height: 1px;
      width: 100%;
      background: #fff;
      border-radius: 1px;
      opacity: 1;
      left: -0.719rem;
      transform: rotate(0deg);
      transition: 0.1s ease-in-out;
    }

    span:nth-child(1) {
      top: 0.5rem;
      width: 0%;
      left: 50%;
    }

    span:nth-child(2) {
      transform: rotate(45deg);
    }

    span:nth-child(3) {
      transform: rotate(-45deg);
    }

    span:nth-child(4) {
      top: 0.5rem;
      width: 0%;
      left: 50%;
    }
  }

  &.open {
    display: block;
    z-index: 9000 !important;
    background-color: #fff;

    .menu-overlay-header {
      z-index: 8000;
    }
  }

  &.fade-out {
    animation: fadeOut 0.1s;
  }

  &.close {
    opacity: 0;
    height: 0;
    min-height: 0;
    border-top: none;
  }

  a:nth-child(0) {
    margin-top: 1rem;
  }
}

